import { Component, OnInit, Inject, OnDestroy, ViewEncapsulation } from '@angular/core';
import { FuseTranslationLoaderService } from '@fuse/services/translation-loader.service';
import { FuseConfigService } from '@fuse/services/config.service';
import { Router } from '@angular/router';
import { locale as spanish } from './i18n/es';
import { SettingsService } from './settings.service';
import { InviteDialogComponent } from './invite-dialog/invite-dialog.component';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar, MatSnackBarHorizontalPosition, MatSnackBarVerticalPosition } from '@angular/material/snack-bar';
import { TranslateService } from '@ngx-translate/core';
import { FuseProgressBarService } from '@fuse/components/progress-bar/progress-bar.service';
import { AuthService } from '../auth/auth.service';
import { FuseConfig } from '@fuse/types';
import { takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';

@Component({
    selector   : 'settings',
    templateUrl: './settings.component.html',
    styleUrls  : ['./settings.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class SettingsComponent implements OnInit, OnDestroy {
    
    organization;
    users: Array<any> = [];
    fuseConfig;
    private _unsubscribeAll: Subject<any>;

    xPos: MatSnackBarHorizontalPosition = 'center';
    yPos: MatSnackBarVerticalPosition = 'top';
    
    constructor(
        private _fuseTranslationLoaderService: FuseTranslationLoaderService,
        private _fuseConfigService: FuseConfigService,
        private fuseProgressBarService: FuseProgressBarService,
        private translateService: TranslateService,
        private router: Router,
        private service: SettingsService,
        private authService: AuthService,
        private snack: MatSnackBar,
        private dialog: MatDialog
    ) {
        this._fuseTranslationLoaderService.loadTranslations(spanish);
        this._unsubscribeAll = new Subject();

        this._fuseConfigService.config = {
            layout: {
                navbar: {
                    hidden: false
                },
                toolbar: {
                    hidden: false
                }
            }
        }
    }

    ngOnInit() {

        this._fuseConfigService.config
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe((config) => {
                this.fuseConfig = config;
            });
    }

    event: any;

    inviteUser() {
        let domain = this.fuseConfig.user.email.substring(this.fuseConfig.user.email.indexOf('@') + 1)
            const dialog = this.dialog.open(InviteDialogComponent, {
                minWidth: '45vw',
                width: '45vw',
                maxWidth: '45vw',
                data: {
                    domain
                }
            });
        
            dialog.afterClosed().subscribe(res => {
                if(typeof res !== 'undefined') {
                    if(res.email.toLowerCase().substring(res.email.indexOf('@') + 1) !== domain.toLowerCase()) {
                        this.snack.open(this.translateService.instant('ERROR.FORBIDDEN_DOMAIN'), 'ERROR', {
                            horizontalPosition: this.xPos,
                            verticalPosition: this.yPos,
                            duration: 5000,
                            panelClass: 'red',
                        });
                        return;
                    }

                    if(this.users.findIndex(u => u.email.toLowerCase() == res.email.toLowerCase()) != -1) {
                        this.snack.open(this.translateService.instant('ERROR.ALREADY_INVITED'), 'ERROR', {
                            horizontalPosition: this.xPos,
                            verticalPosition: this.yPos,
                            duration: 5000,
                            panelClass: 'red',
                        });
                        return;
                    }

                    this.users.push({ firstname: res.firstname, lastname: res.lastname, email: res.email.toLowerCase() });
                }
            });
    }

    sendInvitations() {
        this.fuseProgressBarService.show();
        this.service.sendInvitation({ guests: this.users }).subscribe(res => {
            this.snack.open(res.message, 'INFO', {
                horizontalPosition: this.xPos,
                verticalPosition: this.yPos,
                duration: 5000,
                panelClass: 'green',
            });

            this.users = [];
            this.fuseProgressBarService.hide();
        }, err => {
            this.fuseProgressBarService.hide();
            this.users = [];
            if (this.users.length === 1) {
                this.snack.open(err.error.message, 'ERROR', {
                    horizontalPosition: this.xPos,
                    verticalPosition: this.yPos,
                    duration: 5000,
                    panelClass: 'red',
                });
            } else {
                let errors = [];
                let invited = [];
                let i = 0, j = 0;
                err.error.errors.forEach(e => {
                    if(e.status === 500) {
                        errors[i++] = `${e.email}`
                    } else {
                        invited[j++] = `${e.email}`
                    }
                });

                if(i === err.error.errors.length) { //Completamente fallida
                    this.snack.open(`${this.translateService.instant('ERROR.SOME_FAILED')} ${errors.join(', ')}`, 'ERROR', {
                        horizontalPosition: this.xPos,
                        verticalPosition: this.yPos,
                        duration: 15000,
                        panelClass: 'red',
                    });
                } else { //Parcialmente existosa
                    this.snack.open(
                        `${this.translateService.instant('ERROR.SOME_INVITED')} ${invited.join(', ')}. ${this.translateService.instant('ERROR.SOME_FAILED')} ${errors.join(', ')}`, 'INFO', {
                        horizontalPosition: this.xPos,
                        verticalPosition: this.yPos,
                        duration: 15000,
                        panelClass: 'yellow',
                    });
                }
            }
                
        })
    }

    disconnectOrganization() {
        const dialog = this.dialog.open(DeleteDialogComponent, {
            data: {
            }
        });
      
        dialog.afterClosed().subscribe(res => {
            if(typeof res !== 'undefined') {
                this.service.disconnectOrganization().subscribe(res => {
                    this.authService.logout().subscribe(res => {
                        localStorage.removeItem('authToken');
                        sessionStorage.removeItem('authToken');
                        localStorage.removeItem('authId');
                        sessionStorage.removeItem('authId');
                        localStorage.removeItem('badges');
                        sessionStorage.removeItem('badges');
                        this.authService.isAuthenticated = false;
                        this.router.navigate(['/auth/login']);
                    }, (err) => {
                        localStorage.removeItem('authToken');
                        sessionStorage.removeItem('authToken');
                        localStorage.removeItem('authId');
                        sessionStorage.removeItem('authId');
                        localStorage.removeItem('badges');
                        sessionStorage.removeItem('badges');
                        this.authService.isAuthenticated = false;
                        this.router.navigate(['/auth/login']);
                    });
                });
            }
        });
    }

    removeUser(email) {
        this.users.splice(this.users.findIndex(u => u.email == email), 1);
    }

    ngOnDestroy(): void
    {
        // Unsubscribe from all subscriptions
        this._unsubscribeAll.next();
        this._unsubscribeAll.complete();
    }
}

@Component({
    selector: 'delete-dialog',
    templateUrl: './delete-dialog/delete-dialog.component.html',
  })
  export class DeleteDialogComponent implements OnInit {
  
    constructor(
        public dialogRef: MatDialogRef<any>,
        private router: Router,
        @Inject(MAT_DIALOG_DATA) public data: any
    ) {}
  
    ngOnInit(): void {
  
    }
  
    cancel(): void {
        this.dialogRef.close();
    }
  
    delete(): void {
        
    }
  }