import { OnInit, Component, ViewEncapsulation, Inject } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material/dialog";
import { Router } from "@angular/router";
import { MatSnackBar } from "@angular/material/snack-bar";
import { TranslateService } from "@ngx-translate/core";

@Component({
  selector: 'invite-dialog',
  templateUrl: './invite-dialog.component.html',
  styleUrls: ['./invite-dialog.component.scss']
})

export class InviteDialogComponent implements OnInit {
  
  form: FormGroup;
  error: boolean;
  domain: string;
  
  constructor(
    public dialogRef: MatDialogRef<InviteDialogComponent>,
    private router: Router,
    private snackbar: MatSnackBar,
    private translate: TranslateService,
    private formBuilder: FormBuilder,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {}
    
  ngOnInit() {
    this.form = this.formBuilder.group({
      firstname: ['', [Validators.pattern(/^[a-zA-Z\u00C0-\u024F\u1E00-\u1EFF ]+$/)]],
      lastname: ['', [Validators.pattern(/^[a-zA-Z\u00C0-\u024F\u1E00-\u1EFF ]+$/)]],
      email: ['', [Validators.required, Validators.pattern(/\S+@\S+\.\S+/)]]
    })

    this.error = true;
    this.domain = this.data.domain;
  }

  ngDoCheck() {
    if(this.form.get('email').value.indexOf('@') > -1
      && this.form.get('email').value.toLowerCase().trim().substring(this.form.get('email').value.indexOf('@') + 1) === this.domain)
      this.error = false;
    else this.error = true;
  }
  
  save() {
    this.form.patchValue({
      email: this.form.get('email').value.trim()
    })
    this.dialogRef.close({
      ...this.form.value
    });
  }
}
